import Section from "./Section";
import Button from "./Button";
import { ReactComponent as DocumentIcon } from "../assets/icons/document.svg";
import myimage from "../assets/images/me2.png";
import { Swiper, SwiperSlide } from "swiper/react";
import SwiperCore, { Pagination, Navigation, Autoplay } from "swiper";
import "swiper/swiper-bundle.min.css";
import "swiper/swiper.min.css";

import "./Skills.css";

const Skills = (): JSX.Element => {
  SwiperCore.use([Navigation, Pagination, Autoplay]);

  const skills = [
    { name: "HTML", value: 85 },
    { name: "CSS", value: 90 },
    { name: "React", value: 75 },
    { name: "Angular", value: 75 },
    { name: "Spring Boot", value: 70 },
    { name: "MySQL", value: 65 },
    { name: "MongoDB", value: 65 },
  ];
  return (
    <Section id="skills">
      <div className="skills">
        <h2 className="title">Skills</h2>
        <h3 className="subtitle">My technical orientation</h3>
        <div className="skills-content">
          <div className="container">
            <div className="container-box">
              Using HTML, CSS , Javascript, and frameworks such as React and
              Angular, I enjoy building fast, responsive and interactive web
              apps.
              <br />I am backend oriented on building API services in Java, with
              Spring Boot framework which is used to build stand-alone and
              production ready spring applications.
            </div>

            <div className="container-border"></div>
            <div className="container-border-two"></div>
            <div className="circle"></div>
          </div>

          <div className="skills-container">
            {skills.map(({ name, value }) => (
              <div key={name} className="skill-item">
                <h3 className="skill-item-title">{name}</h3>
                <div className="skill-item-bar">
                  <div
                    className="skill-item-bar-percent"
                    style={{ width: `${value}%` }}
                  ></div>
                </div>
              </div>
            ))}
          </div>
        </div>

        <div className="download-cv-container">
          <div className="download-cv-container-left">
            <h2 className="download-cv-container-title">
              Want more information?
            </h2>
            <p>
              For more details, feel free to download my CV and take a look!
            </p>
            <Button variant="btn-outline">
              <span>Download CV</span>
              <DocumentIcon />
            </Button>
          </div>
          <img src={myimage} alt="" />
        </div>
      </div>
    </Section>
  );
};

export default Skills;
